.experience {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  height: 30vh;
  margin-bottom: 8rem;
}
.achievement {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.circle {
    width: 4rem;
    height: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}
.achievement span:nth-of-type(2){
    color: var(--beige);
}
@media screen and (max-width: 480px) {
    .experience{
        transform: scale(0.7);
    }
}